import Events from './Events';

// Add event binding/triggering to StateMachine
const StateMachine = function () {
};

StateMachine.prototype = $.extend( Events, {

    add( controller ) {
        this.bind( 'change', function ( e, current ) {
            // if dealing with jQuery object make sure we pass the HtmlDOMElement instead
            if ( ( controller instanceof jQuery ) ? controller[0] == current : controller == current ) {
                controller.activate();
            }
            else {
                controller.deactivate();
            }
        } );

        controller.active = () => {
            this.trigger( 'change', controller );
        };
    }
} );

export default StateMachine;




