import '../css/login-registration.scss';

import Hasher from 'Hasher/hasher.js';
import FilteredSearch from 'filter/simpleListFilter';
import 'jquery.quickview';
import Module from './modules/core/Module';
import Model from './modules/core/Model';
import View from './modules/core/View';
import Modal from './plugins/modal';
import StateMachine from './modules/core/StateMachine';
import {getCookie, setCookie, COOKIES} from './util/StorageUtils';
import {formatPhoneUS} from './util/StringUtils';
import {FULLNAME_PATTERN} from './upi/constants';
import RegistrationService from './services/RegistrationService';
import Timer from './modules/ui/Timer';
import {toggleAnimationFor} from './modules/ui/Effects';
import Effects from './modules/ui/Effects';

$( document ).ready( function () {

    var modal = Modal.getInstance();

    $( '.tooltip[href^="/images/"], .tooltip[rel^="/images/"]' ).on( 'click', function ( e ) {
        modal.open( {
            content: '<img src="' + (this.href ? this.href : this.getAttribute( 'rel' )) + '" />',
            width: '452px'
        } );
        return false;
    } );

    (new RegistrationWizard).initialize( '#registrationWizard' );

} );

/* ADboe DTM pagename matrix for each step/id in registration wizard */
const registrationPageNames = {
    '0': 'Login / Registration',
    '1': 'Have you ordered from Ultradent before?',
    'new-customer': 'Sent to new user registration',
    '2': 'Verify User Information',
    '3': 'Please verify your information on file',
    '6': 'Existing customer registration complete',
    '8': 'Registration Locked - Too many attempts',
    'phone-verification': 'Phone Verification',
    'phone-verification-code': 'Phone Verification Code Entry',
    'phone-verification-code-cancelled': 'Phone Verification Code Entry Cancelled',
    'phone-verification-code-timeout': 'Phone Verification Code Entry Timeout',
    'customer-id-verification': 'Customer ID Verification',
    'creditcard-verification': 'Credit Card Verification',
    'customer-verification-failed': 'Verify Customer Information Failed'
};

const track = {
    setViewTitle ( stepId ) {
        const stepTitle = registrationPageNames[stepId];
        upi.dataLayer.registration.setViewTitle( stepTitle ).send();
    }
};
/**
 * Registration Wizard:
 * Provideds a statemachine to change between views
 */
var RegistrationWizard = function () {

    var conf = {
        services: {
            AddressBook: upi.conf.svc.ADDRESSBOOK
        }
    };

    return $.extend( {
        modules: {
            '[data-step="0"]': 'step0',
            '[data-step="1"]': 'step1',
            '[data-step="2"]': 'step2',
            '[data-step="3"]': 'step3',
            '[data-step="6"]': 'step6',
            '[data-step="7"]': 'step7',
            '[data-step="8"]': 'step8'
        },

        privileged_views: {'2': true, '3': true, '6': true},

        events: {
            'keyup input:not(:radio,:checkbox):visible': 'handle_state_change',
            'click [data-action]': 'handle_state_change',
            'change:view': 'change_view'
        },

        initialize: function ( el ) {
            var self = this,
                $aspnetForm = $( '#aspnetForm' );

            this.el = $( el );

            this.before_submit = this.before_submit.bind( this );

            this.update_elements();

            this.validation = $aspnetForm.validate( {
                onblur: true,
                messages: {
                    BillingID: 'Please select a address from the list',
                    ShippingID: 'Please select a address from the list'
                },
                rules: {
                    'newuserpassword1': 'required',
                    'newuserpassword2': {equalTo: '#newuserpassword1'},
                    'accountlastname': {
                        required: true

                    }
                },
                submitHandler: function ( form ) {
                    var step = self.current_step;

                    if ( (step == 0 || step == 3) && !self.isSubmitting ) {
                        self.isSubmitting = true;
                        form.submit();
                    }

                    return false;
                }
            } );

            // add modules to StateMachine and store under instance vars of App
            this.view_state = new StateMachine();
            for ( var key in this.modules ) {
                var name = this.modules[key];
                this[name] = this.add_module( key );
                this.view_state.add( this[name] );
            }

            // add step validation to each view
            $.validator.addMethod( 'pageRequired', this.proxy( function ( value, element ) {
                var $element = $( element ),
                    current_step = this.current_step;

                function match ( index ) {
                    return current_step == index && $element.parents( '[data-step="' + index + '"]' ).length;
                }

                if ( match( current_step ) ) {
                    return !this.validation.optional( element );
                }
                return 'dependency-mismatch';
            } ), $.validator.messages.required );

            $.validator.addMethod( 'email', function ( value, element ) {
                return this.optional( element ) ||
                    /^((([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+(\.([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+)*)|((\x22)((((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(([\x01-\x08\x0b\x0c\x0e-\x1f\x7f]|\x21|[\x23-\x5b]|[\x5d-\x7e]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(\\([\x01-\x09\x0b\x0c\x0d-\x7f]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]))))*(((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(\x22)))@((([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.)+(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))$/i.test(
                        value.trim() );
            } );

            $.validator.addMethod( 'fullName', function ( value, element ) {
                return this.optional( element ) || FULLNAME_PATTERN.test( value );
            }, 'Please enter a valid First and Last name' );

            // set current step based on url fragment
            this.current_step = parseInt( Hasher.getFragment().replace( /\/step\//, '' ) ) || 0;
            // if page rendered with errors set default to step 6
            if ( this.el.parent( '[class*="error-step"]' ).length > 0 ) {
                this.current_step = 3;
            }

            // edge case: attempt to render addressbook from storage if page is refreshed
            // method fails silently if addresses not stored or browser does not support localStorage
            this.generate_address_book();

            this.delegate_events();

            // bind all view change events
            this.el.on( 'change:view', function ( e, i ) {
                Hasher.navigate( '/step/' + i );
                self.toggle_submission_type( parseInt( i, 10 ) );
            } );

            this.go_to_step( this.current_step );

            // important: that these events are bound late for initial load
            Hasher.add( '/step/:id', this.proxy( function ( id ) {
                this.el.trigger( 'change:view', id );
            } ) );
            Hasher.setup();
            return this;
        },

        toggle_submission_type: function ( step ) {
            $( '#SubmissionType' ).attr( 'value', 0 == step ? 'login' : 'registration' );
        },

        add_module: function ( selector ) {
            var mod = (new RegistrationStep);
            mod.initialize( selector, this );
            return mod;
        },

        authenticate_user: function ( params, fn ) {
            console.log( 'passed:', params );
            return RegistrationService.authenticateAccount( params )
                .then( data => {

                    console.log( '@authenticateAccount success', data );

                    if ( data.customerID ) {
                        // FIXME: ask jonathan is this is needed anymore
                        // set :hidden _Customer field
                        $( `[id$=Customer]` ).val( data.customerID );
                    }

                    // fixme: currently formatting based on old api
                    data = {
                        billingAddressBook: data,
                        shippingAddressBook: data
                    };

                    return fn( data );
                } )
                .catch( err => {

                    console.log( '@authenticateAccount failed', err );

                    let step = this['step' + this.current_step];
                    step.toggle_action_button( 'next', 'error', 'Failed, try again' );
                    step.show_error( upi.conf.REGISTRATION_ERROR_MSG );
                } );

            // todo -> if regToken fails an error message or lockout status will be returned
            /*switch ( data.status ) {
                case 0:
                case 1:
                    if ( data.customerID ) {
                        // FIXME: ask jonathan is this is needed anymore
                        // set :hidden _Customer field
                        $( `[id$=Customer]` ).val( data.customerID );
                    }
                    return fn( data );
                case 3: // user locked
                    return this.lock_registration();
                default: {
                    track.setViewTitle( 'customer-verification-failed' );
                    let step = this['step' + this.current_step];
                    step.toggle_action_button( 'next', 'error', 'Failed, try again' );
                    return step.show_error( data.message );
                }
                // submission has errors
            }*/
        },

        registration_locked: function () {
            return (this.get_registration_status()) == 'locked';
        },

        get_registration_status: function () {
            return getCookie( COOKIES.REGISTRATION );
        },

        set_registration_status: function ( status, expires ) {
            setCookie( {cookie: COOKIES.REGISTRATION, value: status}, {expires: expires} );
        },

        lock_registration: function () {
            if ( !this.registration_locked() ) {
                // lock registration process for 10 min
                this.set_registration_status( 'locked', 10 );
            }

            this.el.trigger( 'change:view', 8 );
        },

        init_phone_verification: function ( name, phone ) {
            return RegistrationService.generateTokenByPhone( name, phone );
        },

        generate_address_book: function ( data ) {
            var type = ['billing', 'shipping'];
            // nest addressbook controllers on App.step6 controller for billing & shipping
            // based on data returned from previous step
            for ( var i = 0; i < type.length; i++ ) {
                // match first character in string, lazy
                var bookId = `${type[i]}_addr_book`;
                var addr = this.step6[bookId];
                var label = type[i].replace( /(\w+?)?/, m => m.toUpperCase() );
                var addressList = data && data[`${type[i]}AddressBook`];

                // update existing
                if ( addr && addressList ) {
                    addr.updateAddresses( addressList );
                }
                // create new
                else {
                    addr = this.step6[bookId] = (new AddressBookController);
                    // if data is passed to Model, generate new address book, else attempt to load from localStorage
                    addr.initialize( `#selected${label}Address`, type[i], addressList );
                }

                // render updates
                if ( addr.addresses ) {
                    addr.render();
                }
            }
        },

        /**
         * Determine if is "submit" action and return closet action button if there is one, else return original target
         * @param {HTMLElement} target - original event target to use as search context
         * @param {Boolean} isEnter
         */
        set_target_to_action ( target, isEnter ) {
            if ( isEnter ) {
                // find associated action button if there is one and set as target
                let btn = $( target ).parents( 'fieldset' ).find( '.button[data-action]:first' )[0];
                if ( btn ) {
                    return btn;
                }
            }

            return target;
        },

        go_to_step: function ( i ) {
            this.el.trigger( 'change:view', i );
        },

        change_view: function ( e, i ) {
            // if registration is locked redirect to error page else show requested view

            var view = (this.registration_locked() && this.privileged_views[i]) ? 8 : i;

            this.current_step = view;
            if ( this['step' + view] ) {
                this['step' + view].active();
            }

            // toggle step specific messages
            $( '[data-message-for-step]' ).slideUp( 200 );
            $( '[data-message-for-step="' + this.current_step + '"]' ).slideDown( 200 );

            //Hide the prompt telling the user to log in if they are no longer on the first step (login page)
            if ( view != 0 ) {
                $( '#LoginMessage' ).slideUp( 200 );
            }

            track.setViewTitle( this.current_step );
        },

        // ask service for users addressbook
        before_change: {
            // refactor step ids
            'phone-verification': function ( fn ) {
                // FIXME: these values need to match GQL queries
                var data = {
                    checkVar1: upi.forms.$sp( 'officephone' ).val(),
                    checkVar2: upi.forms.$sp( 'officevalidationcode' ).val(),
                    checkType: 3
                };
                return this.authenticate_user( data, fn );
            },

            'customer-id-verification': function ( fn ) {
                var data = {
                    accountNumber: upi.forms.$sp( 'customerid' ).val(),
                    invoiceId: upi.forms.$sp( 'invoicenumber' ).val()
                };
                return this.authenticate_user( data, fn );
            }
        },

        // validation for current view, delegates to additional view prep method
        // allowing for Async transactions via callback
        // All methods are proxied to context of App
        before_submit: function ( filterName, callback ) {
            if ( this.validation.form() ) {
                var process = this.proxy( this.before_change[filterName] ),
                    fn = (callback) ? this.proxy( callback ) : null;
                // form submission predicate
                return (typeof process === 'function') ? process( fn ) : true;
            }
            // form for current view failed
            return false;
        },

        /**
         * Parse a user submitted full name in to a first and last name using a best guess strategy
         * @param {String} fullName - user submitted full name
         * @return {string}
         * @example
         * "Dr. Jon R. Johnson"
         * returns -> "jon johnson"
         *
         * "jon"
         * returns -> "jon jon"
         */
        sanitizeFullName: function ( fullName ) {
            if ( !fullName ) {
                return '';
            }

            const parts = fullName.trim().toLowerCase().split( ' ' );
            const firstName = /dr\.?/.test( parts[0] ) ? parts[1] : parts[0];
            const lastName = parts[parts.length - 1];

            return firstName + ' ' + lastName;
        },

        handle_user_verification: function ( view, action, isSubmit ) {
            var phoneEl = view.el.find( '.phoneVerification' ),
                errorMsg = null;

            switch ( action ) {
                case 'back':
                    this.go_to_step( 1 );
                    break;

                case 'call-office': {
                    view.toggle_action_button( action, 'info', 'Calling...' );

                    let
                        fullName = upi.forms.$sp( 'accountlastname' ).val(),
                        phoneNum = upi.forms.$sp( 'officephone' ).val();

                    if ( fullName && phoneNum ) {

                        this.init_phone_verification( this.sanitizeFullName( fullName ), phoneNum )
                            .then( data => {

                                if ( data[0].status !== 1 ) {
                                    let err = new Error( 'Could not verify phone number' );
                                    err.response = {data};
                                    throw err;
                                }

                                view.toggle_action_button( 'reset' );
                                phoneEl[0].dataset['state'] = 'step2';
                                view.toggle_form_button();
                                view.resize();

                                $( '#calling-number' )
                                    .html( `<h1>Calling ${ formatPhoneUS( phoneNum ) }</h1>` );

                                track.setViewTitle( 'phone-verification-code' );
                                view.timer = new Timer( document.querySelector( '#countdown-timer' ), {
                                    startAt: 240,
                                    startImmediately: true,
                                    onExpire: () => {
                                        track.setViewTitle( 'phone-verification-code-timeout' );
                                        upi.forms.$sp( 'officevalidationcode' ).val( '' );
                                        phoneEl[0].dataset['state'] = 'step1-retry';
                                        view.toggle_form_button();
                                    }
                                } );

                            } )
                            .catch( error => {
                                const {response} = error;

                                view.toggle_action_button( 'reset' );
                                if ( response ) {
                                    let {status, message} = response.data[0];

                                    switch ( status ) {
                                        case 2:
                                            track.setViewTitle( 'customer-verification-failed' );
                                            return view.show_error( message );
                                        case 3:
                                            return this.lock_registration();

                                        default:
                                            return;
                                    }
                                }
                            } );
                    }
                    else {
                        errorMsg = upi.conf.REGISTRATION_ERROR_MSG;
                    }
                    break;
                }

                case 'backto-call-office':
                    track.setViewTitle( 'phone-verification-code-cancelled' );
                    upi.forms.$sp( 'officevalidationcode' ).val( '' );
                    view.el.find( '.phoneVerification' )[0].dataset['state'] = 'step1';
                    if ( view.timer ) {
                        view.timer.destroy();
                    }
                    break;

                case 'next':
                case (isSubmit): {
                    let verificationType = view.selectedOption;

                    view.toggle_action_button( action, 'info', 'Verifying account...' );

                    this.before_submit( verificationType, function ( data ) {
                        view.toggle_action_button( action, 'success', 'Verified!' );
                        this.generate_address_book( data );
                        this.go_to_step( 3 );

                        if ( view.timer ) {
                            view.timer.destroy();
                        }
                        // reset button state
                        setTimeout( () => view.toggle_action_button( 'reset' ), 2000 );
                    } );
                    break;
                }

                default:
            }

            if ( errorMsg ) {
                view.toggle_action_button( 'reset' );
                view.show_error( errorMsg );
            }
            else {
                view.clear_errors();
            }

            view.toggle_form_button();
            view.resize();
        },

        handle_state_change: function ( e ) {
            var isSubmit = (13 === e.keyCode);
            var target = this.set_target_to_action( e.target, isSubmit );

            if ( 'keyup' === e.type && !isSubmit ) {
                return false;
            }
            if ( $( target ).hasClass( 'disabled' ) ) {
                // note -> this check is required to trigger validation on <enter> for IE
                if ( isSubmit ) {
                    this.validation.form();
                }
                return false;
            }

            var action = target.getAttribute( 'data-action' ),
                stepId = parseInt( this.current_step ),
                view = this['step' + this.current_step];

            // delegate view state
            switch ( stepId ) {
                case 0:
                    // start registration wizard
                    if ( action === 'start' ) {
                        this.go_to_step( 1 );
                    }
                    break;

                case 1:
                    if ( action === 'yes' ) {
                        this.go_to_step( 2 );
                    }
                    else {
                        track.setViewTitle( 'new-customer' );
                        window.location = target.getAttribute( 'data-redirect' );
                    }
                    break;

                case 2: {
                    this.handle_user_verification( view, action, isSubmit );
                    break;
                }

                case 3:
                    // currently addressbook is loaded from localStorage if an address model does not exist
                    if ( action === 'submit' || isSubmit ) {
                        return this.before_submit();
                    }
                    break;

            }

            e.preventDefault();
            return false;
        }

    }, Module );
};

/**
 * RegistrationStep Controller
 */
var RegistrationStep = function () {
    var conf = {
        fx_speed: 200
    };

    return $.extend( {

        elements: {
            '[data-action].button': 'button_action_all'
        },

        events: {
            'keyup input': 'toggle_form_button',
            'change input': 'toggle_form_button',
            'click input': 'toggle_form_button',
            'focusout input.email': 'trim',
            'paste input': 'onpaste',
            'mouseover .button.disabled': 'form_disabled_warning',
            'validation:erroradded': 'resize',
            'validation:errorremoved': 'resize'
        },

        activate: function () {
            var el = this.el;
            // toggle element to enable offsetHeight value
            el.show();
            this.toggle_form_button();
            this.resize();
            el.hide()
                .fadeIn( conf.fx_speed, 'easeOutQuad' )
                .scrollToElement( {delay: 350} );
            this.focusInput();
        },

        deactivate: function () {
            if ( this.el.is( ':visible' ) ) {
                this.el.fadeOut( conf.fx_speed, 'easeOutQuad' );
            }
        },

        trim: function ( e ) {
            e.target.value = e.target.value.trim();
        },

        focusInput: function () {
            this.el.find( 'input:not(:hidden)' ).eq( 0 ).focus();
        },

        initialize: function ( item, app ) {
            this.el = $( item );
            this.delegate_events();
            this.update_elements();

            // attach original label as data attribute to all buttons
            this.button_action_all.each( function () {
                const el = $( this );
                el.data( 'original-text', el.text() );
            } );

            var buttonTabs = this.el.corpus( 'tabs', {
                controls: '.tabNavigation',
                panel: '.tabPanel',
                active_tab: 'none',
                active_class: 'current',
                effect: 'slider'
            } );

            buttonTabs.on( 'corpus.tab.change.control', tab => {
                this.selectedOption = tab.tab_name;
                this.el.find( '.buttonSet' ).addClass( 'has-selection' );
                this.el.find( `[data-tab="${tab.tab_name}"] input:first` ).focus();
                track.setViewTitle( tab.tab_name );
                this.toggle_form_button();
                this.resize();
            } );

            this.App = app;
            // disable submit buttons that require form validation by default, exclude signin button
            this.el.find( '.validate:not([data-action="signin"])' ).addClass( 'disabled' );
        },

        onpaste: function () {
            setTimeout( this.proxy( function () {
                this.toggle_form_button();
            } ), 1 );
        },

        validate: function () {
            var v = this.App.validation,
                el = this.el,
                items = $( v.currentForm ).find( 'input:not(:hidden)' ).filter( function ( i, item ) {
                    return !!item.id && el.find( '#' + item.id ).length;
                } );

            for ( var i = 0; i < items.length; i++ ) {
                if ( !v.slientCheck( items[i] ) ) {
                    return false;
                }
            }
            return true;
        },

        toggle_action_button: function ( action, btnType, label ) {
            const allStates = 'info accept error';

            // if no action reset all action buttons to original ui state
            if ( !action || action === 'reset' ) {
                this.button_action_all.removeClass( allStates ).each( function () {
                    const el = $( this );
                    el.text( el.data( 'original-text' ) );
                } );
                return;
            }

            // find the visible action button by name and set specified state
            this.el
                .find( `[data-action="${action}"].button:visible` )
                .removeClass( allStates )
                .addClass( btnType )
                .text( label );
        },

        toggle_form_button: function ( e ) {
            // exclude signin button
            var buttons = this.el.find( '[data-action]:not([data-action="signin"]).validate' );

            (this.validate())
                ? buttons.removeClass( 'disabled' )
                : buttons.addClass( 'disabled' );
        },

        show_error: function ( msg ) {
            // only render errors in current visible tab
            this.el.find( '[data-tab].current .formError' ).html( msg ).fadeIn();
        },

        clear_errors: function () {
            this.el.find( '.formError' ).html( '' ).fadeOut();
        },

        form_disabled_warning: function ( e ) {
            $( e.target ).showHint( 'Please complete all required fields before continuing' );
        },

        resize: function ( e ) {
            // patch: for <enter> key, triggers validation in turn calling resize on last step in transition
            // if item not visible don't try to resize container
            if ( !this.el.is( ':visible' ) ) {
                return;
            }

            this.App.el.animate( {height: this.el[0].offsetHeight}, conf.fx_speed / 4 );
        }

    }, Module );
};

/**
 * Address Book Controller
 * Stores list of addresses for current user
 */
var AddressBookController = function () {
    return $.extend( {

        events: {
            'click .button': 'change_address'
        },

        addresses: new AddressBook,

        initialize: function ( el, type, addresses ) {
            this.el = $( el );
            this.view = new View( 'addressBook', this );
            this.hasMadeSelection = false;
            this.type = type || 'shipping'; // type of address this controller is for
            // define parent attributes for AddressBook Model for localStorage and retrieval
            this.addresses.attributes =
                ['id', 'address1', 'address2', 'address3', 'address4', 'city', 'state', 'street1', 'zip', 'isDefault'];

            // if new data passed, create addressbook and save locally
            if ( addresses ) {
                this.addresses.populate( addresses );
                this.addresses.save_local( type + '-addressbook' );
            }
            // else attempt to load data from localStorage, if fails return false
            else {
                try {
                    this.addresses.load_local( type + '-addressbook' );
                }
                catch ( e ) {
                    // could not load addressbook: fail silently
                    return false;
                }
            }

            this.delegate_events();

            return true;
        },

        updateAddresses ( addresses ) {
            this.addresses.populate( addresses );
            this.addresses.save_local( this.type + '-addressbook' );
        },

        applyFiltering: function () {
            var self = this,
                $list = this.el.find( '.addresses' );

            // lazy load address filters only once per address list
            var addressFilter = new FilteredSearch( $list, {
                label: 'Filter Addresses',
                hint: 'Anaheim, 90620, or Clementine St',
                placeholder: 'City, ZIP Code, or Street Name'
            } );

            self.quick_view.on( 'hide', addressFilter.clearResults.bind( addressFilter ) );
            self.quick_view.on( 'show', addressFilter.focus.bind( addressFilter ) );
        },

        change_address: function ( e ) {
            var selected = this.addresses.find( e.target.getAttribute( 'data-item' ) ),
                addr_list = this.addresses.all();

            $.each( addr_list, function ( i, item ) {
                item['isDefault'] = false;
                item.update();
            } );

            this.hasMadeSelection = true;
            selected['isDefault'] = true;
            selected.update();
            this.addresses.save_local( this.type + '-addressbook' );

            // re-render addressbook once quickview has completely closed
            this.quick_view.on( 'closed', () => {
                this.render();
            } );
            this.quick_view.trigger( 'hide' );
        },

        render: function () {
            // render entire address book
            var addresses = {
                type: this.type,
                hasMadeSelection: this.hasMadeSelection,
                field_name: (this.type == 'shipping') ? 'ShippingID' : 'BillingID',
                addr: this.addresses.all()
            };

            this.view.whenTemplatesLoaded.then( () => {
                // render addresses
                this.el.html( this.view.render( addresses ) );
                this.el.find( 'input' ).trigger( 'change' ); // manually trigger validation on the generated input fields

                // initialize address book layer
                this.quick_view =
                    this.el.find( '.tooltip-content-wrapper' ).QuickView( {auto_hide: false, trigger_class: 'link1'} );

                if ( this.addresses.all().length > 3 ) {
                    this.applyFiltering();
                }
            } );

        }

    }, Module );
};

var AddressBook = function () {
    return Model.extend();
};



