const defaultProps = {
    startAt: 60,
    startImmediately: false,
    onExpire: () => {
    }
};

const colors = {
    BLUE: '#009cde',
    ORANGE: '#E35205',
    RED: '#d91916',
    LIGHT_GRAY: '#DDDBD9',
    DARK_GRAY: '#333333'
};

class Timer {
    constructor( el, options ) {
        this.DOM = {};
        this.DOM.el = el;

        this.options = {...defaultProps, ...options};

        this.start = this.start.bind( this );
        this.stop = this.stop.bind( this );
        this.destroy = this.destroy.bind( this );

        this.state = {
            expired: false,
            count: parseInt( this.options.startAt, 10 )
        };

        this.init();
    }

    init() {
        if ( this.options.startImmediately ) {
            this.start();
        }

        this.render();
    }

    setState( newState ) {
        const prevState = this.state;
        this.state = {...prevState, ...newState};
    }

    onUpdate() {
        if ( this.state.count <= 0 ) {
            this.expire();
        }

        this.render();
    }

    destroy() {
        this.stop();
        this.DOM.el.innerHTML = '';
    }

    start() {
        this.setState( {expired: false} );
        this.intervalId = setInterval( () => {
            this.setState( {count: this.state.count - 1} );
            this.onUpdate();
        }, 1000 )
    }

    expire() {
        this.setState( {expired: true} );
        this.destroy();
        this.options.onExpire();
    }

    stop() {
        clearInterval( this.intervalId );
    }

    interpolate( range, value ) {
        const delta = range[1] - range[0];
        return range[0] + ((value) * delta);
    }

    getMeterColor( time ) {
        switch ( true ) {
            case ( time <= 15 ):
                return colors.RED;
            case ( time <= 45 ):
                return colors.ORANGE;
            default:
                return colors.BLUE;
        }
    }

    render() {
        if ( this.state.expired ) {
            this.DOM.el.innerHTML = '';
        }

        const
            lineMeter = 128,
            meterLevel = this.interpolate( [0, lineMeter], (this.state.count / this.options.startAt) ),
            meterColor = this.getMeterColor( this.state.count );

        this.DOM.el.innerHTML = `
            <?xml version="1.0" encoding="UTF-8"?>
            <svg viewBox="0 0 48 48" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                <defs>
                    <circle id="path-1" cx="24" cy="24" r="24"></circle>
                    <mask id="mask-2" maskContentUnits="userSpaceOnUse" maskUnits="objectBoundingBox" x="0" y="0" width="48" height="48" fill="white">
                        <use xlink:href="#path-1"></use>
                    </mask>
                </defs>
                <g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                    <g id="timer-display">
                        <use id="empty" stroke="${colors.LIGHT_GRAY}" mask="url(#mask-2)" stroke-width="12" stroke-dasharray="5,1" xlink:href="#path-1"></use>
                        <circle id="fill" stroke="${meterColor}" 
                        stroke-width="6" 
                        stroke-dasharray="${lineMeter}" 
                        stroke-dashoffset="${meterLevel - lineMeter}" 
                        transform="translate(24, 24) rotate(-90) translate(-24, -24)" 
                        cx="24" cy="24" r="20.5" ></circle>
                        <text id="120" text-anchor="middle" x="50%" y="62%" font-family="sans-serif" font-weight="bold" font-size="16" fill="${colors.DARK_GRAY}">
                            ${this.state.count}
                        </text>
                    </g>
                </g>
            </svg>
        `;
    }
}

export default Timer;



